.CreateTodoButton {
  background-color:#103DA0;
  border:none;
  border-radius:50%;
  cursor:pointer;
  font-size:50px;
  position:fixed;
  bottom:24px;
  right:24px;
  font-weight:700;
  color:#fafafa;
  display:flex;
  justify-content:center;
  align-items:center;
  height:64px;
  width:64px;
  transform:rotate(0);
  transition:.3s ease;
  z-index: 1;
  -webkit-appearance: none !important;
}
.CreateTodoButton:hover {
  transform:rotate(224deg)
}